import React, { Component } from 'react'
import './darkMode.css'
import { isNullOrUndefined } from 'util'

class DarkMode extends Component {
    componentDidMount(){
        const html = document.querySelector("html")
        html.classList.toggle("light")
    }

    onClickHandler = e => {
        if(e.target.id==="inner-circle"){
            const toggler = document.getElementById("toggle-btn")
            toggler.classList.toggle("active")
            if(window!==undefined){
                window.localStorage.setItem('dark', JSON.stringify(toggler.classList))
            }
        } else {
        const toggler = e.target
        toggler.classList.toggle("active")
        if(window!==undefined){
            window.localStorage.setItem('dark', JSON.stringify(toggler.classList))
            }
        }
        const mode = document.querySelector("html")
        mode.classList.toggle("light")
        mode.classList.toggle("dark")
    }

    render() {
        if(window===isNullOrUndefined){
            var title = "toggle-btn"
        } else {
            var dark = JSON.parse(window.localStorage.getItem('dark'))
        } 
        if(dark===isNullOrUndefined){
            title = "toggle-btn"
        } else {
            const values = Object.values(dark)
            title = values.join(' ')
        }

        return (
            <div className="darkMode mobile hideThis">dark mode
                <div className={title} id="toggle-btn" onClick={this.onClickHandler}>
                    <div className="inner-circle" id="inner-circle"></div>
                </div>
            </div>
        )
    }
}

export default DarkMode